html {
  width: 100%;
  height: 100%;
  font-size: 16px;
  line-height: 1.4;
  overflow-x: hidden;
  box-sizing: border-box;
}
* {
  box-sizing: inherit;
}
@media screen and (min-width: 481px) {
  html {
    font-size: 20px;
  }
}
.page {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-width: 319px;
  font-family: "Merriweather", serif;
  font-weight: 300;
  color: #0d1335;
  background-color: #fff;
}
h1,
h2,
h3 {
  font-family: "Open Sans", sans-serif;
  font-weight: 700;
}
h1 {
  margin: 1.777rem 0;
  font-size: 2.369em;
}
h2 {
  margin: 1.333rem 0;
  font-size: 1.777em;
}
h3 {
  margin: 1rem 0;
  font-family: "Merriweather", serif;
  font-size: 1.333em;
}
p {
  margin: 0 0;
}
.list {
  margin: 0 0;
  padding: 0 0;
  list-style: none;
}
.link {
  color: currentColor;
  text-decoration: none;
}
.link:active {
  color: #0d1335;
}
.link:hover {
  color: #d32f2f;
}
.link__icon {
  width: 28px;
  height: 28px;
  filter: opacity(0.7);
}
.nav {
  width: 100%;
  padding: 0 1rem;
  font-family: "Open Sans", sans-serif;
  font-weight: 300;
  background-color: rgba(229,246,254,0.71);
  z-index: 999;
}
.nav_affix {
  position: fixed;
  top: 0;
  display: none;
}
.nav_affix-fixed {
  display: flex;
  justify-content: center;
}
.nav__list {
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-around;
  width: 100%;
  margin: 0 0;
}
.nav__item {
  display: flex;
  justify-content: center;
}
.nav__item_logo {
  display: none;
}
.nav__link {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 13px 13px;
}
.nav__link_logo {
  padding: 0 13px;
  font-weight: 700;
  line-height: 54px;
}
/* Hide the social icons and reveal the contacts link */
@media screen and (min-width: 376px) {
  .nav__list {
    justify-content: flex-end;
  }
}
/* 801px for tablet, landscape iPad, lo-res laptops ands desktops */
@media screen and (min-width: 801px) {
  .nav {
    padding: 0 5%;
  }
  .nav__item_logo {
    display: inline-block;
    margin-left: calc(50% - 40px);
    margin-right: auto;
  }
}
.page__section {
  display: flex;
  flex-direction: column;
  align-self: center;
  position: relative;
  margin-bottom: 1rem;
  padding: 0 1rem;
}
.header {
  flex: 1 1 100vh;
  justify-content: center;
}
.header__headline {
  align-self: center;
}
.header__description {
  font-size: 1.333em;
  text-align: center;
}
.about__headline_second {
  margin-top: 0;
}
.about__section {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.about__section_main {
  flex: 3 1 500px;
}
.about__section_secondary {
  flex: 2 3 300px;
}
.about__section_commented {
  color: rgba(13,19,53,0.5);
}
.about__paragraph {
  text-indent: 1.333em;
}
.about__list_quoted::before {
  content: "/*";
}
.about__list_quoted::after {
  content: "*/";
}
.inner,
.project {
  display: flex;
  flex-flow: row wrap;
}
.project__section {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.project__section_main {
  flex: 3 1 500px;
}
.project__section_secondary {
  flex: 2 3 300px;
}
.project__headline {
  margin-top: 0;
}
.project__paragraph {
  margin: 1em 0;
  text-indent: 1.333em;
}
.works__project {
  margin-bottom: 1rem;
}
/* 801px for tablet, landscape iPad, lo-res laptops ands desktops */
@media screen and (min-width: 801px) {
  .page__section {
    padding: 0 5%;
  }
  .header {
    justify-content: flex-start;
    align-self: flex-end;
    width: 50%;
    padding-top: 10vh;
    padding-left: 0;
  }
  .about__section_main,
  .project__section_main {
    margin-right: 3%;
  }
}
.page__bg {
  background: rgba(229,246,254,0.71) url("../images/bg_header_320.png") no-repeat;
  background-position: center center;
  background-size: cover;
}
.page__bg_only-header {
  background-size: 425px auto;
}
.page__mask {
  background-image: linear-gradient(top, rgba(229,246,254,0.6), rgba(229,246,254,0.9));
  z-index: 0;
}
.page__bg_only-header,
.page__mask_only-header {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100vh;
}
/* 480px for smartphones, Android phones, landscape iPhone */
@media screen and (min-width: 481px) {
  .page__bg {
    background-size: cover;
  }
}
/* 801px for tablet, landscape iPad, lo-res laptops ands desktops */
@media screen and (orientation: landscape) and (min-width: 801px) {
  .page__bg_only-header {
    background-size: auto 100%;
    background-position: calc(50% - 240px) 0;
  }
  .page__mask {
    display: none;
  }
}
